  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  padding: 0;
  margin: 0;
}
.app-main{
  padding: 10px;
}
.app-weather-info-heading{
  color: #0099cc;
  font-size: 20px;
  text-align: center;
}
.recent-searches{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding-top: 30px;
}
.recent-heading{
  text-align: center;
}
.recent-searches div{
  margin-top: 1px;
}
@media only screen and (max-width: 600px) {
  .recent-searches {
    padding-left: 40px;
  }
}
.weather-app-user-form{
    margin-top: 10%;
    left: 50%;
    padding: 20px;
    bottom: 0;
    background-color: aliceblue;
    right: 0;
    margin-left: 50%;
    border: solid 1px #322f31;
    width: 200px;
}
.form-main{
    padding-top: 10px;
    display: -webkit-flex;
    display: flex ;
    -webkit-justify-content: center;
            justify-content: center;
}
.button-check {
    padding-left: 10px;
    padding-right: 10px;
}
.button-active{
    background: #0099cc;
    color: #fff;
    border: solid;
    border-radius: 7px;
    cursor: pointer;
}
.button-disabled{
    border: 1px solid #999999;
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
}

  /*the container must be positioned relative:*/
  .autocomplete {
    position: relative;
  }
  
  input {
    border: 1px solid transparent;
    background-color: #f1f1f1;
    padding: 10px;
    font-size: 16px;
  }
  
  input[type=text] {
    background-color: #f1f1f1;
    width: 700px;
    border-radius: 10px;
  }
  
  input[type=submit] {
    background-color: DodgerBlue;
    color: #fff;
    cursor: pointer;
  }
  
  .autocomplete-items {
    position: absolute;
    border: 1px solid #d4d4d4;
    border-bottom: none;
    border-top: none;
    z-index: 99;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    max-height: 400px;
    overflow-x: scroll;
    margin-left: 230px;
  }

  .autocomplete-items div {
    padding: 10px;
    cursor: pointer;
    background-color: #fff; 
    border-bottom: 1px solid #d4d4d4; 
  }
  
  /*when hovering an item:*/
  .autocomplete-items div:hover {
    background-color: #e9e9e9; 
  }
  
  /*when navigating through the items using the arrow keys:*/
  .autocomplete-active {
    background-color: DodgerBlue !important; 
    color: #ffffff; 
  }
.header{
